// Haftalk planlar iin yardmc fonksiyonlar
export const getMonthlyTopicsPool = (monthId) => {
  const month = comprehensiveData.months.find(m => m.id === monthId);
  if (!month) return {};

  // Aylk plandaki tm konular ders baznda dndr
  const topicsPool = {};
  Object.entries(month.subjects).forEach(([subject, topics]) => {
    if (topics.length > 0) {
      topicsPool[subject] = topics.map(topic => ({
        id: `${monthId}-${subject}-${topic}`,
        text: topic,
        subject: subject,
        monthId: monthId,
        assignedWeeks: [] // Hangi haftalara atandn takip et
      }));
    }
  });

  return topicsPool;
};

